.naughty{background-color:#faebd7;border:3px solid #000;border-radius:.5em;padding:1em 2em 1em 2em;position:relative;font-size:1.5em;font-weight:700}.naughty>img{width:2.2em}.naughty>img.TR{position:absolute;top:-1.1em;right:-1.1em;transform:rotateZ(-90deg)}.naughty>img.BR{position:absolute;bottom:-1.1em;right:-1.1em;transform:rotateZ(0deg)}.naughty>img.BL{position:absolute;bottom:-1.1em;left:-1.1em;transform:rotateZ(90deg)}.naughty>img.TL{position:absolute;top:-1.1em;left:-1.1em;transform:rotateZ(180deg)}.naughty_text{display:flex;flex-direction:column}@media screen and (min-width: 1020px){.naughty_text{flex-direction:row}}.naughty_text>span{text-align:center}.naughty_text_first::after{content:none}@media screen and (min-width: 1020px){.naughty_text_first::after{content:" "}}